@import "../variable";

.CollapsiblePanel {
  .Handle {
    width: 100%;
    height: auto;
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    font-size: 22px;
    line-height: 32px;
    color: $color-gray-dark;
    background-color: transparent;
    border: 0;
    text-align: center;

    &:hover {
      outline: none;
    }

    &:focus {
      @extend :hover;
    }

    span {
      box-sizing: content-box;
      display: inline-block;

      padding: 16px 8px;
    }
  }

  .Content {
    display: none;

    &.Show {
      display: block;
    }
  }
}
